import "./App.css";
import { MainLayout } from "./components";
import { Signup } from "./features";
import { Home, Login, Product } from "./pages";
import { UserProvider, ProductProvider } from "./contexts";
import { createTheme, ThemeProvider, CssBaseline } from "@mui/material"; // ✅ 添加 ThemeProvider
import { useSelector } from "react-redux";
import { HashRouter as Router, Route, Routes } from "react-router-dom";

function App() {
  const { mode, primaryColor } = useSelector((state) => state.theme);

  // ✅ 确保 Redux 主题生效
  const theme = createTheme({
    palette: {
      mode, // light 或 dark 模式
      primary: {
        main: primaryColor, // Redux 里的主色
      },
    },
  });

  return (
    <ThemeProvider theme={theme}> {/* ✅ 让 MUI 主题生效 */}
      <CssBaseline /> {/* ✅ 确保全局样式一致 */}
      <UserProvider>
        <ProductProvider>
          <Router>
            <Routes>
              <Route path="/" element={<MainLayout />}>
                <Route index element={<Home />} />
                <Route path="product" element={<Product />} />
                <Route path="login" element={<Login />} />
                <Route path="signup" element={<Signup />} />
              </Route>
            </Routes>
          </Router>
        </ProductProvider>
      </UserProvider>
    </ThemeProvider>
  );
}

export default App;
