import React, { useState, useContext } from "react";
import { UserContext } from "../../contexts";
import { AppBar, Toolbar, IconButton, Typography, Box, Switch } from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import NavbarMenu from "./NavbarMenu";
import NavbarDrawer from "./NavbarDrawer";
import { useSelector, useDispatch } from "react-redux";
import { toggleMode } from "../../redux"; // ✅ 使用正确的 action

export default function Navbar() {
  const { auth } = useContext(UserContext);
  const [mobileOpen, setMobileOpen] = useState(false);
  const dispatch = useDispatch();
  const { mode } = useSelector((state) => state.theme);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  return (
    <>
      {/* 用 Box 包裹 AppBar，确保不遮挡内容 */}
      <Box sx={{ flexGrow: 1 }}>
        <AppBar position="fixed" color="primary">
          <Toolbar>
            <Typography variant="h6" sx={{ flexGrow: 1 }}>
              My Store
            </Typography>

            {/* 主题切换按钮（light / dark） */}
            <Switch
              checked={mode === "dark"} 
              onChange={() => dispatch(toggleMode())} // ✅ 改成 toggleMode
              color="default"
            />

            {/* 普通菜单（大屏显示） */}
            <NavbarMenu auth={auth} />

            {/* 移动端菜单按钮 */}
            <IconButton
              color="inherit"
              edge="end"
              onClick={handleDrawerToggle}
              sx={{ display: { xs: "block", md: "none" } }}
            >
              <MenuIcon />
            </IconButton>
          </Toolbar>
        </AppBar>
      </Box>

      {/* 这里的 Toolbar 需要 **手动设置高度**，避免被遮挡 */}
      <Toolbar sx={{ height: 80 }} />

      <NavbarDrawer auth={auth} open={mobileOpen} onClose={handleDrawerToggle} />
    </>
  );
}
